import SideBar from "./SideBar/SideBar";
import { Typography } from "@material-ui/core";
import { Link, useNavigate } from "react-router-dom";
import { Doughnut, Line } from "react-chartjs-2";
import {
  Chart as ChartJS,
  CategoryScale,
  LinearScale,
  PointElement,
  LineElement,
  Title,
  Tooltip,
  Legend,
  ArcElement,
} from "chart.js";
import { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { adminAuth, userAuth } from "../../../../util/userAuth";
import { useAlert } from "react-alert";
import { getAdminProducts } from "../../../../redux/actions/productAction";
import "./Dashboard.css";
import { getAllUsers } from "../../../../redux/actions/userAction";
import { getAllOrders } from "../../../../redux/actions/orderAction";

const Dashboard = ({ isAdmin }) => {
  const navigate = useNavigate();
  const alert = useAlert();
  const dispatch = useDispatch();
  const { user, isAuthenticated } = useSelector((state) => state.user);
  const { products } = useSelector((state) => state.products);
  const { orders } = useSelector((state) => state.allOrders);
  const { users } = useSelector((state) => state.getAllUsers);

  ChartJS.register(
    CategoryScale,
    LinearScale,
    PointElement,
    LineElement,
    ArcElement,
    Title,
    Tooltip,
    Legend
  );

  let outOfStock = 0;

  products &&
    products.forEach((product) => {
      if (product.stock === 0) outOfStock += 1;
    });


    let amount = [0];
    const labels = [0];
    let totalAmount = 0;
    orders && orders.forEach((order)=>{
      amount.push(order.totalPrice);
      labels.push(String(order.createdAt).substring(0, 10));
      totalAmount+=order.totalPrice;
    });

  const options = {
    responsive: true,
    plugins: {
      legend: {
        position: "top",
      },
      title: {
        display: true,
        text: "Profite Analyizer",
      },
    },
  };

  const lineState = {
    labels: labels,
    datasets: [
      {
        label: "Order Amount",
        backgroundColor: ["tomato"],
        hoverBackgroundColor: ["rgb(197,72,49)"],
        data: amount,
      },
    ],
  };

  const doughnutState = {
    labels: ["Out of Stock", "In Stock"],
    datasets: [
      {
        backgroundColor: ["#00A6B4", "#6800B4"],
        hoverBackgroundColor: ["#4B5000", "#35014F"],
        data: [outOfStock, products.length-outOfStock],
      },
    ],
  };

  useEffect(() => {
    userAuth(isAuthenticated, navigate);
    adminAuth(true, user, navigate, alert);

    dispatch(getAdminProducts());
    dispatch(getAllOrders());
    dispatch(getAllUsers());
  }, [isAdmin, navigate, user, isAuthenticated, alert, dispatch]);

  return (
    <div className="dashboard">
      <SideBar />

      <div className="dashboardContainer">
        <Typography component="h1">Dashboard</Typography>
        <div className="dashboardSummary">
          <div>
            <p>
              Total Price <br /> ₹{totalAmount}
            </p>
          </div>

          <div className="dashboardSummaryBox2">
            <Link to="/admin/products">
              <p>Products</p>
              <p>{products && products.length}</p>
            </Link>
            <Link to="/admin/orders">
              <p>Orders</p>
              <p>{orders && orders.length}</p>
            </Link>
            <Link to="/admin/users">
              <p>Users</p>
              <p>{users && users.length}</p>
            </Link>
          </div>
        </div>
        <div className="charts">
          <div className="lineChart">
            <Line options={options} data={lineState} />
          </div>
          <div className="doughnutChart">
            <Doughnut options={options} data={doughnutState} />
          </div>
        </div>
      </div>
    </div>
  );
};

export default Dashboard;
